import React, { useState, useEffect, useRef } from 'react';
import Quagga from '@ericblade/quagga2';

interface BarcodeRegistrationModalProps {
  isOpen: boolean;
  productId: string;
  productName: string;
  onClose: () => void;
  onSuccess: (barcode: string) => void;
}

type ModalMode = 'choose' | 'scan' | 'manual';
type DetectionStatus = 'none' | 'detecting' | 'ready';

const BarcodeRegistrationModal: React.FC<BarcodeRegistrationModalProps> = ({
  isOpen,
  productId,
  productName,
  onClose,
  onSuccess,
}) => {
  const [mode, setMode] = useState<ModalMode>('choose');
  const [barcode, setBarcode] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [isScanning, setIsScanning] = useState(false);
  const [detectionStatus, setDetectionStatus] = useState<DetectionStatus>('none');
  const [cameraError, setCameraError] = useState('');
  const [scannedCode, setScannedCode] = useState<string | null>(null);
  const [validationError, setValidationError] = useState('');
  const [isScannerLoading, setIsScannerLoading] = useState(false);

  const isActiveRef = useRef(true);
  const hasDetectedRef = useRef(false);
  const videoTrackRef = useRef<MediaStreamTrack | null>(null);

  // Reset state when modal opens/closes
  useEffect(() => {
    if (isOpen) {
      setMode('choose');
      setBarcode('');
      setError('');
      setCameraError('');
      setIsScanning(false);
      setScannedCode(null);
      setValidationError('');
      setIsScannerLoading(false);
      isActiveRef.current = true;
      hasDetectedRef.current = false;
    } else {
      cleanupScanner();
      isActiveRef.current = false;
    }
  }, [isOpen]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      isActiveRef.current = false;
      cleanupScanner();
    };
  }, []);

  const cleanupScanner = () => {
    try {
      Quagga.stop();
      Quagga.offDetected();
      Quagga.offProcessed();
      console.log(' [BarcodeModal] Quagga stopped');
    } catch (err) {
      // Ignore cleanup errors
    }
  };

  // Apply manual focus settings to video track (copied from SalesScanPage)
  const applyManualFocus = async (track: MediaStreamTrack) => {
    try {
      const capabilities = track.getCapabilities() as any;
      console.log(' [BarcodeModal] Camera capabilities:', capabilities);

      const constraints: any = {
        advanced: []
      };

      // 1. DISABLE TORCH - explicitly set to false
      if (capabilities.torch) {
        constraints.advanced.push({ torch: false });
        console.log(' [BarcodeModal] Torch disabled');
      }

      // 2. Set manual focus at 15cm if supported
      if (capabilities.focusMode) {
        if (capabilities.focusMode.includes('manual') && capabilities.focusDistance) {
          const focusDistance = 0.15; // 15cm in meters
          const minDist = capabilities.focusDistance.min || 0;
          const maxDist = capabilities.focusDistance.max || 1;
          const clampedDistance = Math.max(minDist, Math.min(maxDist, focusDistance));

          constraints.advanced.push({ focusMode: 'manual' });
          constraints.advanced.push({ focusDistance: clampedDistance });
          console.log(' [BarcodeModal] Manual focus set to:', clampedDistance);
        } else if (capabilities.focusMode.includes('single-shot')) {
          constraints.advanced.push({ focusMode: 'single-shot' });
          console.log(' [BarcodeModal] Using single-shot focus (fallback)');
        } else if (capabilities.focusMode.includes('continuous')) {
          constraints.advanced.push({ focusMode: 'continuous' });
          console.log(' [BarcodeModal] Using continuous focus (last resort)');
        }
      }

      if (constraints.advanced.length > 0) {
        await track.applyConstraints(constraints);
        console.log(' [BarcodeModal] Camera constraints applied:', constraints.advanced);
      }

      videoTrackRef.current = track;
    } catch (err) {
      console.warn(' [BarcodeModal] Could not apply focus settings:', err);
    }
  };

  // Apply focus to Quagga's video stream after init
  const applyFocusToQuagga = () => {
    try {
      const videoElement = document.querySelector('#barcode-scanner-container video') as HTMLVideoElement;
      if (videoElement && videoElement.srcObject) {
        const stream = videoElement.srcObject as MediaStream;
        const track = stream.getVideoTracks()[0];
        if (track) {
          applyManualFocus(track);
        }
      }
    } catch (err) {
      console.warn(' [BarcodeModal] Could not apply focus to Quagga stream:', err);
    }
  };

  // Validate barcode: must be exactly 13 digits (EAN-13)
  const validateBarcode = (code: string): { valid: boolean; message: string } => {
    if (!code) {
      return { valid: false, message: 'Barcode kosong' };
    }

    if (!/^\d+$/.test(code)) {
      return { valid: false, message: 'Barcode harus berupa angka saja' };
    }

    if (code.length !== 13) {
      return {
        valid: false,
        message: `Barcode harus 13 digit (EAN-13). Terdeteksi ${code.length} digit.`
      };
    }

    return { valid: true, message: '' };
  };

  const initializeScanner = async () => {
    setCameraError('');
    setDetectionStatus('none');
    setScannedCode(null);
    setValidationError('');
    setIsScannerLoading(true);
    hasDetectedRef.current = false;

    console.log(' [BarcodeModal] Initializing Quagga2 scanner...');
    console.log(' [BarcodeModal] User Agent:', navigator.userAgent);

    try {
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        console.error(' [BarcodeModal] No mediaDevices support');
        setCameraError('Browser tidak mendukung akses kamera.');
        setIsScannerLoading(false);
        return;
      }

      // Check available cameras
      try {
        const devices = await navigator.mediaDevices.enumerateDevices();
        const cameras = devices.filter(d => d.kind === 'videoinput');
        console.log(' [BarcodeModal] Available cameras:', cameras.length, cameras.map(c => c.label || 'unnamed'));
      } catch (enumErr) {
        console.warn(' [BarcodeModal] Could not enumerate devices:', enumErr);
      }

      await new Promise(resolve => setTimeout(resolve, 100));

      if (!isActiveRef.current) return;

      const targetElement = document.querySelector('#barcode-scanner-container');
      console.log(' [BarcodeModal] Target element:', targetElement ? 'found' : 'NOT FOUND');

      // SIMPLIFIED constraints for better compatibility
      Quagga.init({
        inputStream: {
          name: 'Live',
          type: 'LiveStream',
          target: targetElement as HTMLElement,
          constraints: {
            facingMode: 'environment',
            // Simplified: just ideal values, let browser choose
            width: { ideal: 1280 },
            height: { ideal: 720 },
          },
          area: {
            // FULL SCAN AREA like SalesScanPage
            top: '5%',
            right: '5%',
            left: '5%',
            bottom: '10%',
          },
        },
        locator: {
          patchSize: 'medium',     // medium is more compatible
          halfSample: true,        // halfSample for speed
        },
        numOfWorkers: Math.min(navigator.hardwareConcurrency || 2, 4),
        frequency: 10,             // 10fps for better detection
        decoder: {
          readers: [
            'ean_reader',
            'ean_8_reader',
            'upc_reader',
            'upc_e_reader',
            'code_128_reader',
            'code_39_reader',
          ],
          multiple: false,
        },
        locate: true,
      }, async (err) => {
        if (err) {
          console.error(' [BarcodeModal] Quagga init error:', err);
          if (err.name === 'NotAllowedError' || err.message?.includes('Permission')) {
            setCameraError('Akses kamera ditolak. Izinkan akses kamera di pengaturan browser.');
          } else if (err.name === 'NotFoundError') {
            setCameraError('Kamera tidak ditemukan.');
          } else {
            setCameraError(`Gagal mengakses kamera: ${err.message || 'Unknown error'}`);
          }
          setIsScannerLoading(false);
          return;
        }

        console.log(' [BarcodeModal] Quagga initialized');

        applyFocusToQuagga();

        Quagga.start();
        console.log(' [BarcodeModal] Quagga started');

        if (isActiveRef.current) {
          setIsScanning(true);
          setIsScannerLoading(false);
        }
      });

      // Detection status feedback
      let processedCount = 0;
      Quagga.onProcessed((result) => {
        if (!isActiveRef.current || hasDetectedRef.current) return;

        processedCount++;
        // Log every 30 frames to show scanner is working
        if (processedCount % 30 === 0) {
          console.log(` [BarcodeModal] Frame ${processedCount}: processing...`,
            result?.boxes?.length ? `${result.boxes.length} boxes` : 'no boxes',
            result?.codeResult?.code ? `code: ${result.codeResult.code}` : '');
        }

        if (result) {
          if (result.boxes && result.boxes.length > 0) {
            const box = result.boxes[0];
            if (box && box.length >= 4) {
              if (result.codeResult && result.codeResult.code) {
                setDetectionStatus('ready');
              } else {
                setDetectionStatus('detecting');
              }
            } else {
              setDetectionStatus('detecting');
            }
          } else {
            setDetectionStatus('none');
          }
        }
      });

      // Barcode detection with validation
      let detectionCount = 0;
      Quagga.onDetected((result) => {
        if (!isActiveRef.current || hasDetectedRef.current) return;

        detectionCount++;
        const code = result.codeResult.code;
        const format = result.codeResult.format;

        // Log ALL detections to understand what's happening
        console.log(` [BarcodeModal] Detection #${detectionCount}: code=${code}, format=${format}`);

        if (!code) return;

        const errors = result.codeResult.decodedCodes
          ?.filter((x: any) => x.error !== undefined)
          ?.map((x: any) => x.error) || [];

        const avgError = errors.length > 0
          ? errors.reduce((a: number, b: number) => a + b, 0) / errors.length
          : 1;

        // VERY RELAXED: Accept most detections for testing
        // avgError < 0.35 = confidence > 65%
        if (avgError < 0.35) {
          console.log(' [BarcodeModal] BARCODE DETECTED:', code,
            'confidence:', ((1 - avgError) * 100).toFixed(0) + '%');

          hasDetectedRef.current = true;
          cleanupScanner();
          setIsScanning(false);

          if (navigator.vibrate) {
            navigator.vibrate(200);
          }

          const validation = validateBarcode(code);
          setScannedCode(code);

          if (!validation.valid) {
            setValidationError(validation.message);
          } else {
            handleSubmitBarcode(code);
          }
        } else {
          console.log(' [BarcodeModal] Detection rejected:', code,
            'error:', avgError.toFixed(2),
            'confidence:', ((1 - avgError) * 100).toFixed(0) + '%');
        }
      });

    } catch (err: any) {
      console.error(' [BarcodeModal] Scanner error:', err);
      setCameraError('Gagal memulai scanner.');
      setIsScannerLoading(false);
    }
  };

  const handleModeSelect = async (selectedMode: 'scan' | 'manual') => {
    setMode(selectedMode);
    setError('');
    setValidationError('');
    setScannedCode(null);

    if (selectedMode === 'scan') {
      await initializeScanner();
    }
  };

  const handleBack = () => {
    cleanupScanner();
    setIsScanning(false);
    setCameraError('');
    setMode('choose');
    setBarcode('');
    setError('');
    setScannedCode(null);
    setValidationError('');
  };

  const handleRescan = async () => {
    setScannedCode(null);
    setValidationError('');
    hasDetectedRef.current = false;
    await initializeScanner();
  };

  const handleSwitchToManual = () => {
    setMode('manual');
    setScannedCode(null);
    setValidationError('');
    if (scannedCode) {
      setBarcode(scannedCode);
    }
  };

  const handleSubmitBarcode = async (code: string) => {
    const validation = validateBarcode(code);
    if (!validation.valid) {
      setError(validation.message);
      return;
    }

    setIsLoading(true);
    setError('');

    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`/api/products/${productId}/barcode`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ barcode: code }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Gagal menyimpan barcode');
      }

      onSuccess(code);
    } catch (err: any) {
      console.error('[BarcodeModal] Submit error:', err);
      setError(err.message || 'Gagal menyimpan barcode');
    } finally {
      setIsLoading(false);
    }
  };

  const handleManualSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    handleSubmitBarcode(barcode.trim());
  };

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget && mode !== 'scan') {
      onClose();
    }
  };

  const getBorderColor = () => {
    switch (detectionStatus) {
      case 'ready': return 'border-green-500';
      case 'detecting': return 'border-yellow-500';
      default: return 'border-red-500';
    }
  };

  const getGlowColor = () => {
    switch (detectionStatus) {
      case 'ready': return 'rgba(34, 197, 94, 0.8)';
      case 'detecting': return 'rgba(234, 179, 8, 0.6)';
      default: return 'rgba(239, 68, 68, 0.4)';
    }
  };

  if (!isOpen) return null;

  // FULL SCREEN SCAN MODE - exactly like SalesScanPage
  if (mode === 'scan') {
    return (
      <div className="fixed inset-0 z-[200] bg-gray-900 flex flex-col">
        {/* Header */}
        <div className="bg-white border-b border-gray-200 p-4 flex items-center gap-3">
          <button
            onClick={handleBack}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors active:bg-gray-200"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
          </button>
          <div className="flex-1">
            <h1 className="font-semibold text-lg">Daftarkan Barcode</h1>
            <p className="text-sm text-gray-500 truncate">{productName}</p>
          </div>
          <button
            onClick={onClose}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Scanner View - FULL SCREEN */}
        <div className="flex-1 relative bg-black">
          {/* Scanner Container */}
          <div
            id="barcode-scanner-container"
            className="absolute inset-0"
            style={{ overflow: 'hidden' }}
          />

          {/* Camera Error */}
          {cameraError && (
            <div className="absolute inset-0 flex items-center justify-center text-white p-6 z-10 bg-black">
              <div className="text-center max-w-md">
                <svg className="w-12 h-12 mx-auto mb-3 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
                <p className="mb-4">{cameraError}</p>
                <button
                  onClick={initializeScanner}
                  className="px-6 py-3 bg-amber-500 text-white rounded-lg hover:bg-amber-600 active:bg-amber-700 transition-colors flex items-center gap-2 mx-auto"
                >
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                  </svg>
                  Coba Lagi
                </button>
              </div>
            </div>
          )}

          {/* Loading State */}
          {isScannerLoading && (
            <div className="absolute inset-0 flex items-center justify-center text-white z-10 bg-black">
              <div className="text-center">
                <svg className="w-12 h-12 mx-auto mb-3 animate-pulse" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
                <p>Memulai scanner...</p>
              </div>
            </div>
          )}

          {/* Scan overlay with targeting box */}
          {isScanning && !scannedCode && (
            <div className="absolute inset-0 pointer-events-none z-10">
              <div className="absolute inset-0 flex items-center justify-center">
                <div
                  className="relative transition-all duration-200"
                  style={{
                    width: '85vw',
                    height: '60vh',
                    maxWidth: '500px',
                    maxHeight: '400px',
                    boxShadow: `0 0 20px ${getGlowColor()}`
                  }}
                >
                  {/* Corner brackets */}
                  <div className={`absolute top-0 left-0 w-10 h-10 border-t-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                  <div className={`absolute top-0 right-0 w-10 h-10 border-t-4 border-r-4 ${getBorderColor()} transition-colors`}></div>
                  <div className={`absolute bottom-0 left-0 w-10 h-10 border-b-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                  <div className={`absolute bottom-0 right-0 w-10 h-10 border-b-4 border-r-4 ${getBorderColor()} transition-colors`}></div>

                  {/* Scan line animation - only when ready */}
                  {detectionStatus === 'ready' && (
                    <div
                      className="absolute left-2 right-2 h-0.5 bg-green-500"
                      style={{
                        boxShadow: '0 0 10px rgba(34, 197, 94, 0.8)',
                        animation: 'scanLine 1s ease-in-out infinite'
                      }}
                    />
                  )}
                </div>
              </div>
            </div>
          )}

          {/* Invalid Barcode Modal */}
          {scannedCode && validationError && (
            <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/80">
              <div className="bg-white rounded-xl p-6 mx-4 max-w-sm w-full shadow-2xl">
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                  </svg>
                  <p className="text-sm font-medium text-gray-700 mb-1">Barcode Tidak Valid</p>
                  <p className="text-xs text-gray-500 mb-2">{validationError}</p>
                  <p className="text-xs text-gray-400 mb-4 font-mono">{scannedCode}</p>
                  <div className="flex gap-2">
                    <button
                      onClick={handleRescan}
                      className="flex-1 px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50"
                    >
                      Scan Ulang
                    </button>
                    <button
                      onClick={handleSwitchToManual}
                      className="flex-1 px-4 py-2 bg-gray-800 text-white rounded-lg text-sm font-medium hover:bg-gray-700"
                    >
                      Input Manual
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Saving overlay */}
          {isLoading && (
            <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/80">
              <div className="bg-white rounded-xl p-6 mx-4 max-w-sm w-full shadow-2xl">
                <div className="text-center">
                  <svg className="animate-spin w-8 h-8 mx-auto mb-3 text-gray-600" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  <p className="text-sm text-gray-600">Menyimpan barcode...</p>
                </div>
              </div>
            </div>
          )}

          {/* CSS for scan line animation */}
          <style>{`
            @keyframes scanLine {
              0% { top: 10%; }
              50% { top: 90%; }
              100% { top: 10%; }
            }

            #barcode-scanner-container video {
              width: 100% !important;
              height: 100% !important;
              object-fit: cover !important;
            }

            #barcode-scanner-container canvas {
              display: none !important;
            }
          `}</style>

          {/* Info Box with Tips */}
          {isScanning && !scannedCode && (
            <div className="absolute top-4 left-4 right-14 bg-black/60 backdrop-blur-sm rounded-lg p-3 text-white text-xs z-20">
              <p className="font-semibold mb-1"> Barcode Scanner</p>
              <ul className="space-y-0.5 opacity-80">
                <li> Posisikan barcode di dalam kotak</li>
                <li> <strong>Jarak tepat: 15cm</strong> dari kamera</li>
                <li> <span className="text-red-400">Merah</span> = tidak ada, <span className="text-yellow-400">Kuning</span> = mendekat, <span className="text-green-400">Hijau</span> = siap!</li>
              </ul>
            </div>
          )}

          {/* Debug: Test Button */}
          {isScanning && !scannedCode && (
            <button
              onClick={() => {
                const testBarcode = '8997032821272'; // Esse Change Applemint
                console.log(' [BarcodeModal] Manual test with barcode:', testBarcode);
                hasDetectedRef.current = true;
                cleanupScanner();
                setIsScanning(false);
                if (navigator.vibrate) navigator.vibrate(200);
                const validation = validateBarcode(testBarcode);
                setScannedCode(testBarcode);
                if (!validation.valid) {
                  setValidationError(validation.message);
                } else {
                  handleSubmitBarcode(testBarcode);
                }
              }}
              className="absolute top-4 right-4 px-3 py-1.5 bg-yellow-500 text-black text-xs font-medium rounded-lg z-30 shadow-lg"
            >
               Test
            </button>
          )}

          {/* Status Indicator */}
          {isScanning && !scannedCode && (
            <div className="absolute bottom-32 left-0 right-0 text-center text-white z-20">
              <div className={`inline-block px-6 py-3 rounded-full backdrop-blur-sm ${
                detectionStatus === 'ready' ? 'bg-green-500/60' :
                detectionStatus === 'detecting' ? 'bg-yellow-500/60' :
                'bg-black/60'
              }`}>
                <div className="flex items-center gap-2">
                  <div className={`w-2 h-2 rounded-full animate-pulse ${
                    detectionStatus === 'ready' ? 'bg-white' :
                    detectionStatus === 'detecting' ? 'bg-white' :
                    'bg-red-500'
                  }`}></div>
                  <p className="text-sm font-medium">
                    {detectionStatus === 'ready' ? 'Siap scan!' :
                     detectionStatus === 'detecting' ? 'Mendekat...' :
                     'Cari barcode...'}
                  </p>
                </div>
              </div>
            </div>
          )}
        </div>

        {/* Bottom Actions */}
        <div className="bg-white border-t border-gray-200 p-4">
          <div className="flex gap-3">
            <button
              onClick={handleBack}
              className="flex-1 px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50 active:bg-gray-100 transition-colors font-medium"
            >
              Kembali
            </button>
            <button
              onClick={handleSwitchToManual}
              className="flex-1 px-6 py-3 bg-gray-800 text-white rounded-lg hover:bg-gray-700 active:bg-gray-900 transition-colors font-medium"
            >
              Input Manual
            </button>
          </div>
        </div>
      </div>
    );
  }

  // MODAL MODE (choose / manual)
  return (
    <div
      className="fixed inset-0 z-[200] flex items-center justify-center bg-black/50"
      onClick={handleBackdropClick}
    >
      <div className="bg-white rounded-2xl w-full max-w-md mx-4 overflow-hidden shadow-2xl">
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-gray-200">
          <div className="flex items-center gap-2">
            {mode === 'manual' && (
              <button
                onClick={handleBack}
                className="p-1 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </button>
            )}
            <h3 className="font-semibold text-lg">
              {mode === 'choose' ? 'Daftarkan Barcode' : 'Ketik Barcode'}
            </h3>
          </div>
          <button
            onClick={onClose}
            className="p-1 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Content */}
        <div className="p-4">
          {/* Choose Mode */}
          {mode === 'choose' && (
            <div className="text-center">
              <p className="text-gray-600 mb-4">{productName}</p>
              <div className="flex gap-3">
                <button
                  onClick={() => handleModeSelect('scan')}
                  className="flex-1 py-4 px-4 border-2 border-gray-200 rounded-xl hover:border-gray-400 hover:bg-gray-50 transition-all flex flex-col items-center gap-2"
                >
                  <span className="text-2xl"></span>
                  <span className="font-medium text-sm">Scan</span>
                </button>
                <button
                  onClick={() => handleModeSelect('manual')}
                  className="flex-1 py-4 px-4 border-2 border-gray-200 rounded-xl hover:border-gray-400 hover:bg-gray-50 transition-all flex flex-col items-center gap-2"
                >
                  <span className="text-2xl"></span>
                  <span className="font-medium text-sm">Ketik Manual</span>
                </button>
              </div>
            </div>
          )}

          {/* Manual Mode */}
          {mode === 'manual' && (
            <form onSubmit={handleManualSubmit}>
              <div className="mb-4">
                <p className="text-sm text-gray-600 mb-3">
                  Produk: <span className="font-medium">{productName}</span>
                </p>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Masukkan kode barcode (13 digit):
                </label>
                <input
                  type="text"
                  inputMode="numeric"
                  pattern="[0-9]*"
                  value={barcode}
                  onChange={(e) => setBarcode(e.target.value.replace(/\D/g, ''))}
                  placeholder="8991234567890"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-gray-400 text-center font-mono text-lg"
                  maxLength={13}
                  autoFocus
                  disabled={isLoading}
                />
                <p className="text-xs text-gray-500 mt-1">
                  Format EAN-13: Harus tepat 13 digit angka
                </p>
                {barcode && barcode.length !== 13 && (
                  <p className="text-xs text-yellow-600 mt-1">
                    {barcode.length} digit dimasukkan, butuh {13 - barcode.length} digit lagi
                  </p>
                )}
              </div>

              {error && (
                <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-lg">
                  <p className="text-sm text-red-700">{error}</p>
                </div>
              )}

              <button
                type="submit"
                disabled={isLoading || !barcode || barcode.length !== 13}
                className="w-full py-3 bg-gray-800 text-white rounded-lg font-medium hover:bg-gray-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors flex items-center justify-center gap-2"
              >
                {isLoading ? (
                  <>
                    <svg className="animate-spin w-5 h-5" fill="none" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                    </svg>
                    Menyimpan...
                  </>
                ) : (
                  <>
                    <span></span>
                    Simpan Barcode
                  </>
                )}
              </button>
            </form>
          )}
        </div>
      </div>
    </div>
  );
};

export default BarcodeRegistrationModal;
