import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import NavSidebar from '../components/app/NavSidebar';
import TenantSidebar from '../components/app/WorkspaceSidebar';
import ChatPanel from '../components/app/ChatPanel';
import { getUserTenants } from '../utils/api';

// Tenant metadata interface
interface Tenant {
  id: string;
  name: string;
  avatar?: string;
  status?: 'online' | 'offline';
  lastMessage?: string;
  lastMessageTime?: string;
}

const Dashboard = () => {
  const location = useLocation();
  const navigate = useNavigate();

  // Tenant list with metadata
  const [tenants, setTenants] = useState<Tenant[]>([]);
  const [isLoadingTenants, setIsLoadingTenants] = useState(true);

  // Check if mobile
  const isMobile = window.innerWidth < 768;

  // Active tenant as object (not string)
  const [activeTenant, setActiveTenant] = useState<Tenant | null>(() => {
    // Desktop: restore from localStorage
    if (!isMobile) {
      const savedId = localStorage.getItem('activeTenantId');
      return savedId ? { id: savedId, name: 'Loading...' } : null;
    }
    // Mobile: check URL params or localStorage
    const params = new URLSearchParams(window.location.search);
    const tenantFromUrl = params.get('tenant');
    const savedTenantId = localStorage.getItem('mobile_active_tenant');

    if (tenantFromUrl) {
      return { id: tenantFromUrl, name: 'Loading...' };
    } else if (savedTenantId) {
      return { id: savedTenantId, name: 'Loading...' };
    }
    return null;
  });

  // Mobile chat state - restore from localStorage or URL
  const [showChat, setShowChat] = useState(() => {
    if (!isMobile) return false;

    const params = new URLSearchParams(window.location.search);
    const tenantFromUrl = params.get('tenant');
    const actionFromUrl = params.get('action');
    const savedState = localStorage.getItem('mobile_chat_state');

    // If URL has tenant or action, show chat
    if (tenantFromUrl || actionFromUrl) return true;
    // If localStorage says chat was open, restore it
    if (savedState === 'chat') return true;

    return false;
  });

  // Handle action parameter from URL (e.g., returning from camera with action=openPurchaseForm)
  useEffect(() => {
    if (!isMobile) return;

    const params = new URLSearchParams(location.search);
    const tenantFromUrl = params.get('tenant');
    const actionFromUrl = params.get('action');

    // If URL has action parameter, ensure chat is shown
    if (actionFromUrl && tenantFromUrl) {
      console.log('[Dashboard] Detected action parameter, ensuring chat is shown:', actionFromUrl);
      setShowChat(true);
      localStorage.setItem('mobile_chat_state', 'chat');
      localStorage.setItem('mobile_active_tenant', tenantFromUrl);
    }
  }, [location.search, isMobile]);

  // Fetch tenant list on mount
  useEffect(() => {
    const fetchTenants = async () => {
      try {
        setIsLoadingTenants(true);
        
        // Fetch tenants from API (supports all authenticated users)
        const tenantList = await getUserTenants();
        console.log('[Dashboard] Fetched tenants from API:', tenantList);
        
        // Add Milky Assistant as first tenant (always available)
        const allTenants: Tenant[] = [
          {
            id: 'milky-assistant',
            name: 'Milky Assistant',
            avatar: '',
            status: 'online',
            lastMessage: 'Siap membantu Anda!',
            lastMessageTime: 'Now'
          },
          ...tenantList
        ];
        
        console.log('[Dashboard] All tenants (with Milky Assistant):', allTenants.map(t => ({ id: t.id, name: t.name })));
        setTenants(allTenants);
        
        // Restore active tenant from localStorage/URL
        if (!isMobile) {
          // Desktop: restore from localStorage
          const savedId = localStorage.getItem('activeTenantId');
          if (savedId) {
            const savedTenant = allTenants.find(t => t.id === savedId);
            setActiveTenant(savedTenant || allTenants[0]);
          } else {
            // Default to Milky Assistant
            setActiveTenant(allTenants[0]);
          }
        } else {
          // Mobile: restore from URL params or localStorage
          const params = new URLSearchParams(window.location.search);
          const tenantFromUrl = params.get('tenant');
          const actionFromUrl = params.get('action');
          const savedTenantId = localStorage.getItem('mobile_active_tenant');

          if (tenantFromUrl) {
            const tenant = allTenants.find(t => t.id === tenantFromUrl);
            if (tenant) {
              setActiveTenant(tenant);
              setShowChat(true);
              localStorage.setItem('mobile_chat_state', 'chat');
              localStorage.setItem('mobile_active_tenant', tenantFromUrl);
              console.log('[Dashboard] Set active tenant from URL:', tenant.id, 'action:', actionFromUrl);
            } else {
              // Tenant not found in list but URL has tenant - create temporary tenant
              console.log('[Dashboard] Tenant not in list, creating temp:', tenantFromUrl);
              const tempTenant = { id: tenantFromUrl, name: 'Loading...' };
              setActiveTenant(tempTenant);
              setShowChat(true);
              localStorage.setItem('mobile_chat_state', 'chat');
              localStorage.setItem('mobile_active_tenant', tenantFromUrl);
            }
          } else if (savedTenantId) {
            const tenant = allTenants.find(t => t.id === savedTenantId);
            if (tenant) {
              setActiveTenant(tenant);
              // Restore chat state from localStorage
              const savedState = localStorage.getItem('mobile_chat_state');
              setShowChat(savedState === 'chat');
            }
          }
        }
      } catch (error) {
        console.error('Failed to fetch tenants:', error);
        // Fallback to Milky Assistant only
        const fallbackTenant: Tenant = {
          id: 'milky-assistant',
          name: 'Milky Assistant',
          avatar: '',
          status: 'online'
        };
        setTenants([fallbackTenant]);
        if (!isMobile) {
          setActiveTenant(fallbackTenant);
        }
      } finally {
        setIsLoadingTenants(false);
      }
    };

    fetchTenants();
  }, []);

  // Save active tenant ID to localStorage
  useEffect(() => {
    if (activeTenant) {
      if (!isMobile) {
        localStorage.setItem('activeTenantId', activeTenant.id);
      }
      // Mobile persistence is handled in handleTenantSelect
    }
  }, [activeTenant, isMobile]);

  const handleTenantSelect = (tenantId: string) => {
    const selectedTenant = tenants.find(t => t.id === tenantId);
    if (selectedTenant) {
      setActiveTenant(selectedTenant);
      setShowChat(true); // Show chat on mobile

      // Mobile: persist to localStorage and update URL
      if (isMobile) {
        localStorage.setItem('mobile_chat_state', 'chat');
        localStorage.setItem('mobile_active_tenant', tenantId);
        // Update URL for proper back navigation
        navigate(`/app?tenant=${tenantId}`, { replace: true });
      }
    } else {
      console.error('[handleTenantSelect] Tenant not found:', tenantId, 'Available:', tenants.map(t => t.id));
    }
  };

  const handleBackToList = () => {
    setShowChat(false); // Back to tenant list on mobile

    // Mobile: update localStorage and clear URL
    if (isMobile) {
      localStorage.setItem('mobile_chat_state', 'list');
      localStorage.removeItem('mobile_active_tenant');
      // Clear URL params
      navigate('/app', { replace: true });
    }
  };

  return (
    <>
      {/* Mobile Layout - Stack */}
      <div className="md:hidden h-screen overflow-hidden">
        {!showChat ? (
          <TenantSidebar
            tenants={tenants}
            activeTenant={activeTenant}
            onSelectTenant={handleTenantSelect}
            isLoading={isLoadingTenants}
          />
        ) : (
          <ChatPanel
            activeTenant={activeTenant}
            onBack={handleBackToList}
            isMobile={true}
          />
        )}
      </div>

      {/* Desktop Layout - ABSOLUTE POSITIONING (Most Reliable) */}
      <div 
        className="hidden md:block"
        style={{
          position: 'relative',
          height: '100vh',
          width: '100vw',
          overflow: 'hidden'
        }}
      >
        {/* Navbar - Absolute positioned, fixed 64px width */}
        <div style={{ 
          position: 'absolute', 
          left: 0, 
          top: 0, 
          width: '64px', 
          height: '100%',
          overflow: 'hidden'
        }}>
          <NavSidebar />
        </div>

        {/* Tenant Sidebar - Absolute positioned, fixed 400px width */}
        <div style={{ 
          position: 'absolute', 
          left: '64px', 
          top: 0, 
          width: '400px', 
          height: '100%',
          overflow: 'hidden'
        }}>
          <TenantSidebar 
            tenants={tenants}
            activeTenant={activeTenant}
            onSelectTenant={handleTenantSelect}
            isLoading={isLoadingTenants}
          />
        </div>

        {/* Chat Panel - Absolute positioned, fills remaining space */}
        <div style={{ 
          position: 'absolute', 
          left: '464px',  // 64px + 400px
          right: 0,       // Stretch to right edge - GUARANTEED FULL WIDTH
          top: 0, 
          height: '100%',
          overflow: 'hidden'
        }}>
          <ChatPanel 
            activeTenant={activeTenant}
            onBack={handleBackToList}
          />
        </div>
      </div>
    </>
  );
};

export default Dashboard;