import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import Quagga from '@ericblade/quagga2';

// Detection status for visual feedback
type DetectionStatus = 'none' | 'detecting' | 'ready';

const SalesScanPage: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const isActiveRef = useRef(true);
  const hasDetectedRef = useRef(false);
  const videoTrackRef = useRef<MediaStreamTrack | null>(null);

  // Get context and tenantId from route state or URL params
  const routeState = location.state as any;
  const params = new URLSearchParams(location.search);
  const context = routeState?.context || params.get('context') || 'sale';
  const tenantId = routeState?.tenantId || params.get('tenant');
  const returnTo = routeState?.returnTo || `/app?tenant=${tenantId}&action=openPurchaseForm`;

  const [hasPermission, setHasPermission] = useState<boolean | null>(null);
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(true);
  const [isScanning, setIsScanning] = useState(false);
  const [scannedCode, setScannedCode] = useState<string | null>(null);
  const [isLookingUp, setIsLookingUp] = useState(false);
  const [lookupError, setLookupError] = useState<string>('');
  const [showSuccess, setShowSuccess] = useState(false);
  const [detectionStatus, setDetectionStatus] = useState<DetectionStatus>('none');

  // Initialize scanner with proper lifecycle management
  useEffect(() => {
    isActiveRef.current = true;
    hasDetectedRef.current = false;
    initializeScanner();

    return () => {
      // Cleanup on unmount
      isActiveRef.current = false;
      cleanupScanner();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const cleanupScanner = () => {
    try {
      Quagga.stop();
      Quagga.offDetected();
      Quagga.offProcessed();
      console.log(' [Scanner] Quagga stopped');
    } catch (err) {
      console.error('Scanner cleanup error:', err);
    }
  };

  // Apply manual focus settings to video track (called after Quagga init)
  const applyManualFocus = async (track: MediaStreamTrack) => {
    try {
      const capabilities = track.getCapabilities() as any;
      console.log(' [Scanner] Camera capabilities:', capabilities);

      const constraints: any = {
        advanced: []
      };

      // 1. DISABLE TORCH - explicitly set to false
      if (capabilities.torch) {
        constraints.advanced.push({ torch: false });
        console.log(' [Scanner] Torch disabled');
      }

      // 2. Set manual focus at 15cm if supported
      if (capabilities.focusMode) {
        if (capabilities.focusMode.includes('manual') && capabilities.focusDistance) {
          // Manual focus mode with fixed distance
          const focusDistance = 0.15; // 15cm in meters (some cameras use diopters)
          const minDist = capabilities.focusDistance.min || 0;
          const maxDist = capabilities.focusDistance.max || 1;

          // Clamp to valid range
          const clampedDistance = Math.max(minDist, Math.min(maxDist, focusDistance));

          constraints.advanced.push({ focusMode: 'manual' });
          constraints.advanced.push({ focusDistance: clampedDistance });
          console.log(' [Scanner] Manual focus set to:', clampedDistance);
        } else if (capabilities.focusMode.includes('single-shot')) {
          // Fallback to single-shot
          constraints.advanced.push({ focusMode: 'single-shot' });
          console.log(' [Scanner] Using single-shot focus (fallback)');
        } else if (capabilities.focusMode.includes('continuous')) {
          // Last resort - continuous
          constraints.advanced.push({ focusMode: 'continuous' });
          console.log(' [Scanner] Using continuous focus (last resort)');
        }
      }

      // Apply constraints if any
      if (constraints.advanced.length > 0) {
        await track.applyConstraints(constraints);
        console.log(' [Scanner] Camera constraints applied:', constraints.advanced);
      }

      videoTrackRef.current = track;
    } catch (err) {
      console.warn(' [Scanner] Could not apply focus settings:', err);
    }
  };

  // Apply focus to Quagga's video stream after init
  const applyFocusToQuagga = () => {
    try {
      const videoElement = document.querySelector('#scanner-container video') as HTMLVideoElement;
      if (videoElement && videoElement.srcObject) {
        const stream = videoElement.srcObject as MediaStream;
        const track = stream.getVideoTracks()[0];
        if (track) {
          applyManualFocus(track);
        }
      }
    } catch (err) {
      console.warn(' [Scanner] Could not apply focus to Quagga stream:', err);
    }
  };

  const initializeScanner = async () => {
    console.log(' [Scanner] Initializing Quagga2 scanner...');
    setIsLoading(true);
    setError('');
    setDetectionStatus('none');

    try {
      // Request camera permission directly - this is triggered by user click on camera icon
      // so it's a valid user gesture for permission request
      console.log(' [Scanner] Requesting camera permission (user-triggered)...');

      // First check if we have camera support
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        setError('Browser tidak mendukung akses kamera.');
        setHasPermission(false);
        setIsLoading(false);
        return;
      }

      // Wait for DOM element to be ready
      await new Promise(resolve => setTimeout(resolve, 100));

      if (!isActiveRef.current) return;

      // Initialize Quagga2 - it will create its own video element
      Quagga.init({
        inputStream: {
          name: 'Live',
          type: 'LiveStream',
          target: document.querySelector('#scanner-container') as HTMLElement,
          constraints: {
            facingMode: 'environment',
            width: { min: 640, ideal: 1280, max: 1920 },
            height: { min: 480, ideal: 720, max: 1080 },
            // NO torch here
          },
          area: {
            // EXPANDED: 85% viewport - minimal margins
            top: '5%',
            right: '5%',
            left: '5%',
            bottom: '10%',
          },
        },
        locator: {
          patchSize: 'large',      // CHANGED: large for better tolerance
          halfSample: false,       // CHANGED: full resolution for better detection
        },
        numOfWorkers: navigator.hardwareConcurrency || 4,
        frequency: 5,              // CHANGED: 5fps for better accuracy
        decoder: {
          readers: [
            'ean_reader',
            'ean_8_reader',
            'upc_reader',
            'upc_e_reader',
            'code_128_reader',
            'code_39_reader',
          ],
          multiple: false,
        },
        locate: true,
      }, async (err) => {
        if (err) {
          console.error(' [Scanner] Quagga init error:', err);

          // Check for specific permission errors
          if (err.name === 'NotAllowedError' || err.message?.includes('Permission')) {
            setError('Akses kamera ditolak. Mohon izinkan akses kamera di pengaturan browser.');
          } else if (err.name === 'NotFoundError') {
            setError('Kamera tidak ditemukan. Pastikan perangkat memiliki kamera.');
          } else {
            setError(`Gagal mengakses kamera: ${err.message || 'Unknown error'}`);
          }

          setHasPermission(false);
          setIsLoading(false);
          return;
        }

        console.log(' [Scanner] Quagga initialized - camera permission granted');
        setHasPermission(true);

        // Apply focus settings to Quagga's video stream
        applyFocusToQuagga();

        // Start scanning
        Quagga.start();
        console.log(' [Scanner] Quagga started');

        if (isActiveRef.current) {
          setIsScanning(true);
          setIsLoading(false);
        }
      });

      // Set up processed handler for visual feedback
      Quagga.onProcessed((result) => {
        if (!isActiveRef.current || hasDetectedRef.current) return;

        if (result) {
          // Check if barcode box was found
          if (result.boxes && result.boxes.length > 0) {
            // Calculate box coverage
            const box = result.boxes[0];
            if (box && box.length >= 4) {
              if (result.codeResult && result.codeResult.code) {
                // Barcode detected - show ready immediately
                // SIMPLIFIED: Any detection = ready (remove size check)
                setDetectionStatus('ready');
              } else {
                // Box found but no code yet = detecting
                setDetectionStatus('detecting');
              }
            } else {
              setDetectionStatus('detecting');
            }
          } else {
            setDetectionStatus('none');
          }
        }
      });

      // Set up detection handler
      Quagga.onDetected((result) => {
        if (!isActiveRef.current || hasDetectedRef.current) return;

        const code = result.codeResult.code;
        if (!code) return;

        // Validate detection confidence only (removed coverage check)
        const errors = result.codeResult.decodedCodes
          ?.filter((x: any) => x.error !== undefined)
          ?.map((x: any) => x.error) || [];

        const avgError = errors.length > 0
          ? errors.reduce((a: number, b: number) => a + b, 0) / errors.length
          : 1;

        // RELAXED: Only check error rate, no coverage requirement
        // avgError < 0.25 = confidence > 75% (was 0.12 = 88%)
        if (avgError < 0.25) {
          console.log(' [Scanner] BARCODE DETECTED:', code,
            'confidence:', ((1 - avgError) * 100).toFixed(0) + '%');
          hasDetectedRef.current = true;
          onScanSuccess(code);
        } else {
          console.log(' [Scanner] Detection rejected:', code,
            'error:', avgError.toFixed(2),
            'confidence:', ((1 - avgError) * 100).toFixed(0) + '%');
        }
      });

    } catch (err: any) {
      console.error(' [Scanner] Initialization error:', err);

      if (!isActiveRef.current) return;

      setHasPermission(false);
      setIsLoading(false);

      if (err.name === 'NotAllowedError') {
        setError('Akses kamera ditolak. Mohon izinkan akses kamera di pengaturan browser.');
      } else if (err.name === 'NotFoundError') {
        setError('Kamera tidak ditemukan. Pastikan perangkat memiliki kamera.');
      } else {
        setError(`Gagal mengakses kamera: ${err.message || 'Unknown error'}`);
      }
    }
  };

  const onScanSuccess = async (decodedText: string) => {
    if (!isActiveRef.current || scannedCode) return;

    console.log(' [Scanner] Processing barcode:', decodedText);

    setScannedCode(decodedText);
    setIsScanning(false);
    setDetectionStatus('none');

    // Stop scanner
    cleanupScanner();

    // Haptic feedback
    if (navigator.vibrate) {
      navigator.vibrate(200);
    }

    // Lookup product
    await lookupProduct(decodedText);
  };

  // Debug: Manual test function
  const handleTestBarcode = () => {
    const testBarcode = '5061011083487';
    console.log(' [Scanner] Manual test with barcode:', testBarcode);
    hasDetectedRef.current = true;
    onScanSuccess(testBarcode);
  };

  const lookupProduct = async (barcode: string) => {
    setIsLookingUp(true);
    setLookupError('');

    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`/api/products/barcode/${barcode}?tenant_id=${tenantId}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const product = await response.json();
        console.log('[SalesScanPage] Product found:', product);

        setShowSuccess(true);
        setIsLookingUp(false);

        await new Promise(resolve => setTimeout(resolve, 800));

        if (!isActiveRef.current) return;

        navigate(returnTo, {
          state: {
            action: 'purchase',
            productData: {
              name: product.nama_produk || product.name,
              barcode: barcode,
              unit: product.satuan || product.unit,
            }
          },
          replace: true
        });
      } else if (response.status === 404) {
        setLookupError(`Produk tidak ditemukan untuk barcode: ${barcode}`);
        setIsLookingUp(false);
      } else {
        setLookupError('Gagal mencari produk. Silakan coba lagi.');
        setIsLookingUp(false);
      }
    } catch (err) {
      console.error('[SalesScanPage] Product lookup error:', err);
      setLookupError('Gagal mencari produk. Silakan coba lagi.');
      setIsLookingUp(false);
    }
  };

  const handleRescan = async () => {
    setScannedCode(null);
    setLookupError('');
    setShowSuccess(false);
    hasDetectedRef.current = false;

    // Reinitialize scanner
    await initializeScanner();
  };

  const handleBack = () => {
    if (context === 'purchase' && tenantId) {
      navigate(`/app?tenant=${tenantId}&action=openPurchaseForm`, { replace: true });
    } else if (tenantId) {
      navigate(`/app?tenant=${tenantId}`, { replace: true });
    } else {
      navigate(-1);
    }
  };

  const handleManualInput = () => {
    if (context === 'purchase' && tenantId) {
      navigate(`/app?tenant=${tenantId}&action=openPurchaseForm`, { replace: true });
    } else if (tenantId) {
      navigate(`/app?tenant=${tenantId}`, { replace: true });
    } else {
      navigate(-1);
    }
  };

  // Get border color based on detection status
  const getBorderColor = () => {
    switch (detectionStatus) {
      case 'ready': return 'border-green-500';
      case 'detecting': return 'border-yellow-500';
      default: return 'border-red-500';
    }
  };

  const getGlowColor = () => {
    switch (detectionStatus) {
      case 'ready': return 'rgba(34, 197, 94, 0.8)';
      case 'detecting': return 'rgba(234, 179, 8, 0.6)';
      default: return 'rgba(239, 68, 68, 0.4)';
    }
  };

  return (
    <div className="min-h-screen bg-gray-900 flex flex-col">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 p-4 flex items-center gap-3">
        <button
          onClick={handleBack}
          className="p-2 hover:bg-gray-100 rounded-lg transition-colors active:bg-gray-200"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
        </button>
        <div className="flex-1">
          <h1 className="font-semibold text-lg">
            {context === 'purchase' ? 'Scan Produk Pembelian' : 'Scan Produk'}
          </h1>
          <p className="text-sm text-gray-500">Arahkan kamera ke barcode produk</p>
        </div>
      </div>

      {/* Scanner View */}
      <div className="flex-1 relative bg-black">
        {/* Scanner Container - Quagga will inject video here */}
        <div
          id="scanner-container"
          className="absolute inset-0"
          style={{
            overflow: 'hidden',
          }}
        />

        {/* Scan overlay with targeting box - color changes based on detection */}
        {isScanning && !scannedCode && (
          <div className="absolute inset-0 pointer-events-none z-10">
            <div className="absolute inset-0 flex items-center justify-center">
              <div
                className="relative transition-all duration-200"
                style={{
                  width: '85vw',      // EXPANDED: 85% viewport width
                  height: '60vh',     // EXPANDED: 60% viewport height
                  maxWidth: '500px',  // Cap for larger screens
                  maxHeight: '400px',
                  boxShadow: `0 0 20px ${getGlowColor()}`
                }}
              >
                {/* Corner brackets - color based on status */}
                <div className={`absolute top-0 left-0 w-10 h-10 border-t-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                <div className={`absolute top-0 right-0 w-10 h-10 border-t-4 border-r-4 ${getBorderColor()} transition-colors`}></div>
                <div className={`absolute bottom-0 left-0 w-10 h-10 border-b-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                <div className={`absolute bottom-0 right-0 w-10 h-10 border-b-4 border-r-4 ${getBorderColor()} transition-colors`}></div>

                {/* Scan line animation - only when ready */}
                {detectionStatus === 'ready' && (
                  <div
                    className="absolute left-2 right-2 h-0.5 bg-green-500"
                    style={{
                      boxShadow: '0 0 10px rgba(34, 197, 94, 0.8)',
                      animation: 'scanLine 1s ease-in-out infinite'
                    }}
                  />
                )}
              </div>
            </div>
          </div>
        )}

        {/* Loading State */}
        {isLoading && (
          <div className="absolute inset-0 flex items-center justify-center text-white z-10 bg-black">
            <div className="text-center">
              <svg className="w-12 h-12 mx-auto mb-3 animate-pulse" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
              </svg>
              <p>Memulai scanner...</p>
            </div>
          </div>
        )}

        {/* Permission Denied State */}
        {!isLoading && hasPermission === false && (
          <div className="absolute inset-0 flex items-center justify-center text-white p-6 z-10 bg-black">
            <div className="text-center max-w-md">
              <svg className="w-12 h-12 mx-auto mb-3 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
              <p className="mb-4">{error}</p>
              <button
                onClick={initializeScanner}
                className="px-6 py-3 bg-amber-500 text-white rounded-lg hover:bg-amber-600 active:bg-amber-700 transition-colors flex items-center gap-2 mx-auto"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                Coba Lagi
              </button>
              <button
                onClick={handleBack}
                className="mt-3 px-6 py-2 text-sm text-gray-400 hover:text-white transition-colors block mx-auto"
              >
                Kembali
              </button>
            </div>
          </div>
        )}

        {/* Product Lookup Modal */}
        {scannedCode && (
          <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/80">
            <div className="bg-white rounded-xl p-6 mx-4 max-w-sm w-full shadow-2xl">
              {isLookingUp ? (
                <div className="text-center">
                  <svg className="animate-spin w-8 h-8 mx-auto mb-3 text-gray-600" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  <p className="text-sm text-gray-600">Mencari produk...</p>
                  <p className="text-xs text-gray-400 mt-1 font-mono">{scannedCode}</p>
                </div>
              ) : showSuccess ? (
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <p className="text-sm text-gray-700 font-medium">Produk ditemukan!</p>
                  <p className="text-xs text-gray-400 font-mono">{scannedCode}</p>
                </div>
              ) : lookupError ? (
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                  </svg>
                  <p className="text-sm text-gray-700 mb-1">{lookupError}</p>
                  <p className="text-xs text-gray-400 mb-4 font-mono">{scannedCode}</p>
                  <div className="flex gap-2">
                    <button
                      onClick={handleRescan}
                      className="flex-1 px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50"
                    >
                      Scan Ulang
                    </button>
                    <button
                      onClick={handleManualInput}
                      className="flex-1 px-4 py-2 bg-gray-800 text-white rounded-lg text-sm font-medium hover:bg-gray-700"
                    >
                      Input Manual
                    </button>
                  </div>
                </div>
              ) : (
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <p className="text-sm text-gray-700">Barcode terdeteksi!</p>
                  <p className="text-xs text-gray-400 font-mono">{scannedCode}</p>
                </div>
              )}
            </div>
          </div>
        )}

        {/* CSS for scan line animation */}
        <style>{`
          @keyframes scanLine {
            0% { top: 10%; }
            50% { top: 90%; }
            100% { top: 10%; }
          }

          /* Style Quagga video element */
          #scanner-container video {
            width: 100% !important;
            height: 100% !important;
            object-fit: cover !important;
          }

          #scanner-container canvas {
            display: none !important;
          }
        `}</style>

        {/* Info Box with Tips */}
        {isScanning && !scannedCode && (
          <div className="absolute top-4 left-4 right-4 bg-black/60 backdrop-blur-sm rounded-lg p-3 text-white text-xs z-20">
            <p className="font-semibold mb-1"> Barcode Scanner</p>
            <ul className="space-y-0.5 opacity-80">
              <li> Posisikan barcode di dalam kotak</li>
              <li> <strong>Jarak tepat: 15cm</strong> dari kamera</li>
              <li> <span className="text-red-400">Merah</span> = tidak ada, <span className="text-yellow-400">Kuning</span> = mendekat, <span className="text-green-400">Hijau</span> = siap!</li>
            </ul>
          </div>
        )}

        {/* Debug: Test Button */}
        {isScanning && !scannedCode && (
          <button
            onClick={handleTestBarcode}
            className="absolute top-4 right-4 px-3 py-1.5 bg-yellow-500 text-black text-xs font-medium rounded-lg z-30 shadow-lg"
          >
             Test
          </button>
        )}

        {/* Status Indicator */}
        {isScanning && !scannedCode && (
          <div className="absolute bottom-32 left-0 right-0 text-center text-white z-20">
            <div className={`inline-block px-6 py-3 rounded-full backdrop-blur-sm ${
              detectionStatus === 'ready' ? 'bg-green-500/60' :
              detectionStatus === 'detecting' ? 'bg-yellow-500/60' :
              'bg-black/60'
            }`}>
              <div className="flex items-center gap-2">
                <div className={`w-2 h-2 rounded-full animate-pulse ${
                  detectionStatus === 'ready' ? 'bg-white' :
                  detectionStatus === 'detecting' ? 'bg-white' :
                  'bg-red-500'
                }`}></div>
                <p className="text-sm font-medium">
                  {detectionStatus === 'ready' ? 'Siap scan!' :
                   detectionStatus === 'detecting' ? 'Mendekat...' :
                   'Cari barcode...'}
                </p>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Bottom Actions */}
      <div className="bg-white border-t border-gray-200 p-4">
        <div className="flex gap-3">
          <button
            onClick={handleBack}
            className="flex-1 px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50 active:bg-gray-100 transition-colors font-medium"
          >
            Kembali
          </button>
          <button
            onClick={handleManualInput}
            className="flex-1 px-6 py-3 bg-gray-800 text-white rounded-lg hover:bg-gray-700 active:bg-gray-900 transition-colors font-medium"
          >
            Input Manual
          </button>
        </div>
      </div>
    </div>
  );
};

export default SalesScanPage;
